*{
    padding: 0;
    margin: 0;
}
.form-container{
    position:absolute;
    width:600px;
    height: 600px;
    margin: auto;
    background-color: rgb(113, 100, 84);
    line-height: 10px;
    padding: 20px 25px 25px 25px;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
  
}
.form-container{
    box-shadow: 10px 15px 30px 20px   rgb(101, 93, 82) ;
}
.form-container input{
    height: 30px;
    width: 500px;
    padding-bottom: 10px;
    font-size: 30px;
   
    border: none;
}
.form-container label{
    font-size: 20px;
  
}
.form-container button{
    width: 100px;
    font-size: 25px;
    color: white;
    height: 70px;
    background-color: rgb(54, 88, 72);
}

